import { Fragment, useState, useEffect } from "react";
import moment from "moment";
import { Typography, Box, Paper, FormGroup, FormControlLabel, Checkbox, Divider } from "@mui/material";
import PersonRoundedIcon from "@mui/icons-material/PersonRounded";

import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { StaticDatePicker } from "@mui/x-date-pickers/StaticDatePicker";

import { Scheduler } from "@aldabil/react-scheduler";

import Navbar from "../../Components/Navbar/Navbar";
import { makeResourceFunc, makeEventFunc } from "./data";
import { getUserByIdFunc } from "../../actions/user";
import { getAppointmentFuncForScheduler } from "../../actions/appointment";
import auth from "../../auth";
import BlockSchedulerDialog from "./dialog";
import ConfirmDialog from "../../Components/Confirm/ConfirmDialog";

const SchedulerPage = () => {
  const [key, setKey] = useState(0);
  const [events, setEvents] = useState([]);
  const [resources, setResources] = useState([]);
  const [openConfirm, setOpenConfirm] = useState(false);
  const [user, setUser] = useState({ _id: "", fullName: "", meetings: [] });
  const [selectedDate, setSelectedDate] = useState(moment());
  const [value, setValue] = useState(new Date());
  const [appointments, setAppointments] = useState([]);
  const [openBlockSchedluderDialog, setOpenBlockSchedulerDialog] = useState(false);
  const [selectedMeet, setSelectedMeet] = useState([]);
  const [filteredSchedule, setFilteredSchedule] = useState([]);
  const [filteredEvents, setFilteredEvents] = useState([]);

  useEffect(() => {
    const subId = auth.getSub();
    getUserByIdFunc(subId)
      .then((res) => {
        setUser(res.data);
      })
      .catch((e) => {
        console.log("Error: " + e);
      });
  }, []);
  const handleChangeEvents = (ids) => {
    const appts = appointments.filter((appt) => !ids.includes(appt._id));
    setAppointments(appts ? appts : []);
    console.log(ids, appts, appointments);
    const newEvents = makeEventFunc(appts ? appts : []);
    setEvents(newEvents);
  };
  useEffect(() => {
    const fetchAppts = async () => {
      getAppointmentFuncForScheduler(user._id, user.timezone)
        .then((res) => {
          setAppointments(res.data);
          const newResources = makeResourceFunc(res.data);
          const newEvents = makeEventFunc(res.data);
          setEvents(newEvents ? newEvents : []);
          setResources(newResources ? newResources : []);
          setFilteredEvents(newEvents);
          console.log(events, "=======", filteredEvents);
        })
        .catch((e) => {
          console.error("Error: " + e);
        });
    };
    if (user._id) fetchAppts();
    setSelectedMeet(user?.meetings?.map((meeting) => meeting.meetingName));
  }, [user]);

  const schedulerConfirm = (event, action) => {
    if (action === "edit") {
      return event;
    } else {
      return event;
    }
  };
  useEffect(() => {
    const filtered = events.filter((e) => selectedMeet.includes(e.meetingName));
    console.log(events, selectedMeet, filtered);
    setKey(key + 1);
    setFilteredEvents(filtered);
  }, [selectedMeet, events]);

  return (
    <Fragment>
      <Navbar picture={user.picture ? user.picture : ""} name={user.fullName} />
      <Box display="flex">
        <Box width="30%" display="flex" flexDirection="column" padding={1}>
          <Box>
            <Paper elevation={3}>
              <LocalizationProvider dateAdapter={AdapterDayjs}>
                <StaticDatePicker
                  value={value}
                  className="no-buttons-block-dialog"
                  onChange={(date) => {
                    setSelectedDate(date);
                    setOpenConfirm(false);
                    setOpenBlockSchedulerDialog(false);
                    const filtered = appointments.filter(
                      (event) =>
                        moment(event?.apptTime).local().format("YYYY-MM-DD") ===
                        moment(date.toString()).format("YYYY-MM-DD")
                    );
                    setFilteredSchedule(filtered);

                    filtered.length === 0 ? setOpenConfirm(true) : setOpenBlockSchedulerDialog(true);
                  }}
                />
              </LocalizationProvider>
            </Paper>
          </Box>
          <Box mt={1}>
            <Paper elevation={3}>
              <Box p={2}>
                Appointment Type
                <FormGroup>
                  <FormControlLabel
                    key={`${"all"}-checkbox`}
                    control={
                      <Checkbox
                        checked={selectedMeet.length === user.meetings.length}
                        onChange={() => {
                          setSelectedMeet(
                            selectedMeet.length === user.meetings.length
                              ? []
                              : user?.meetings?.map((meeting) => meeting.meetingName)
                          );
                        }}
                      />
                    }
                    label={"All meetings"}
                  />
                  <Divider />
                  {user.meetings.map((meeting, index) => (
                    <FormControlLabel
                      key={`${index}-checkbox`}
                      control={
                        <Checkbox
                          checked={selectedMeet.includes(meeting.meetingName)}
                          onChange={() => {
                            setSelectedMeet([meeting.meetingName]);
                          }}
                        />
                      }
                      label={meeting.meetingName}
                    />
                  ))}
                </FormGroup>
              </Box>
            </Paper>
          </Box>
        </Box>
        <Box width="70%" padding={1}>
          <Paper elevation={3}>
            {resources.length > 0 && filteredEvents.length > 0 && (
              <Scheduler
                key={key}
                events={filteredEvents ? filteredEvents : []}
                resources={resources ? resources : []}
                resourceFields={{
                  idField: "_id",
                  textField: "fullName",
                  subTextField: "phone",
                  // avatarField: "fullName",
                  colorField: "color",
                }}
                onConfirm={schedulerConfirm}
                fields={[
                  {
                    name: "_id",
                    type: "select",
                    default: resources[0]._id,
                    options: resources.map((res) => {
                      return {
                        id: res._id,
                        text: `${res.fullName} (${res.phone})`,
                        value: res._id, //Should match "name" property
                      };
                    }),
                    config: { label: "Assignee", required: true },
                  },
                ]}
                viewerExtraComponent={(fields, events) => {
                  return (
                    <div>
                      {fields?.map((field, i) => {
                        if (field.name === "_id") {
                          const admin = field.options.find((fe) => fe.id === events._id);
                          return (
                            <Typography
                              key={i}
                              style={{ display: "flex", alignItems: "center" }}
                              color="textSecondary"
                              variant="caption"
                              noWrap
                            >
                              <PersonRoundedIcon /> {admin.text}
                            </Typography>
                          );
                        } else {
                          return "";
                        }
                      })}
                    </div>
                  );
                }}
              />
            )}
          </Paper>
        </Box>
      </Box>
      <BlockSchedulerDialog
        openBlockSchedluderDialog={openBlockSchedluderDialog}
        setOpenBlockSchedulerDialog={setOpenBlockSchedulerDialog}
        selectedDate={selectedDate}
        user={user}
        filteredSchedule={filteredSchedule}
        handleChangeEvents={handleChangeEvents}
      />
      <ConfirmDialog
        title="Confirm"
        content="There is no any schedule"
        onCancel={() => {
          setOpenConfirm(false);
        }}
        onOK={() => {
          setOpenConfirm(false);
        }}
        openDialog={openConfirm}
      />
    </Fragment>
  );
};

export default SchedulerPage;
