import React, { useState } from "react";
import { toast } from "react-toastify";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Box,
  Select,
  Table,
  TableBody,
  TableRow,
  TableCell,
  MenuItem,
  Grid,
  FormControl,
  Checkbox,
  FormControlLabel,
} from "@mui/material";
import useStylesDialog from "./stylesDialog";
import "./newEventDialog.css";

import { getUserByIdFunc, makeSchedularEvent, updateSchedularEvent } from "../../actions/user";
import { getUsers } from "../../actions/user";

const NewEventDialog = ({ open, handleClose, sub, editMeeting, loginedUser, id, renderNewMeeting }) => {
  const classes = useStylesDialog();
  const [name, setName] = useState("");
  const [duration, setDuration] = useState(15);
  const [user, setUser] = React.useState({});
  const [days, setDays] = useState([]);
  const [expiredTime, setExpiredTime] = useState(24);
  const [startDate, setStartDate] = useState(new Date());

  const [endDate, setEndDate] = useState(new Date());
  const [repeat, setRepeat] = useState(1);
  const [meetingName, setMeetingName] = useState("");
  const [allDay, setAllDay] = useState(false);

  const handleSubmit = () => {
    const sendInfos = {
      duration: duration,
      repeat: repeat,
      startDate: startDate,
      allDay: allDay,
      days: days,
      endDate: endDate,
      meetingName: meetingName,
      expiredTime: expiredTime,
    };
    makeSchedularEvent(sub, sendInfos)
      .then((res) => {
        if (res.status === 200) {
          renderNewMeeting(res.data.docs);
          handleClose();
        } else {
          handleClose();
          toast.error("Duplicated ScheduleEvent");
        }
      })
      .catch((e) => console.log(e));
  };
  // const handleChangeDay = (selectedDay) => {
  //   console.log(selectedDay);
  //   setDays([...days, { selectedDay: !days?.selectedDay }]);
  // };
  React.useEffect(() => {
    setUser(loginedUser);
    setDays(loginedUser ? loginedUser?.availability?.days : []);
  }, [loginedUser]);
  React.useEffect(() => {
    setAllDay(editMeeting?.allDay);
    setMeetingName(editMeeting?.meetingName);
    setRepeat(editMeeting?.repeat);
    setDays(editMeeting?.days);
    setDuration(editMeeting?.duration);
    setStartDate(editMeeting.startDate);
    setEndDate(editMeeting?.endDate);
    setExpiredTime(editMeeting?.expiredTime);
  }, [editMeeting]);
  console.log(editMeeting, allDay);
  const handleUpdate = () => {
    const updateInfos = {
      _id: editMeeting?._id,
      duration: duration,
      repeat: repeat,
      startDate: startDate,
      allDay: allDay,
      days: days,
      endDate: endDate,
      meetingName: meetingName,
      expiredTime: expiredTime,
    };
  };

  return (
    <Dialog fullWidth={true} maxWidth="sm" open={open} onClose={handleClose} className="new-event-dialog">
      <DialogTitle style={{ textAlign: "center" }}>{id ? "Edit Event" : "Nenu Terminart"}</DialogTitle>
      <DialogContent>
        <Grid container spacing={4}>
          <Grid item md={6} xs={12} sm={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Absicht</Box>
              <Select
                fullWidth
                value={meetingName}
                onChange={(e) => {
                  console.log(e.target.value);
                  setMeetingName(e.target.value);
                }}
              >
                <MenuItem key={"absicht"} value="cleaning">
                  Cleaning
                </MenuItem>
              </Select>
            </Box>
          </Grid>
          <Grid item md={6} xs={12} sm={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Behandler</Box>
              <Select fullWidth value={user?._id} onChange={(e) => setUser(e.target.value)}>
                <MenuItem key={`${user?._id}`} value={user?._id}>
                  Dr.{user?.fullName}
                </MenuItem>
              </Select>
            </Box>
          </Grid>
          <Grid item md={6} xs={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Zeitintervall</Box>
              <Select fullWidth value={duration} onChange={(e) => setDuration(e.target.value)}>
                <MenuItem key={"15"} value={15}>
                  15 min
                </MenuItem>
                <MenuItem key={"30"} value={30}>
                  30 min
                </MenuItem>
                <MenuItem key={"45"} value={45}>
                  45 min
                </MenuItem>
                <MenuItem key={"60"} value={60}>
                  60 min
                </MenuItem>
              </Select>
            </Box>
          </Grid>
          <Grid item md={6} xs={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Stornierungsfrist</Box>
              <Select fullWidth value={expiredTime} onChange={(e) => setExpiredTime(e.target.value)}>
                <MenuItem key="time" value={24}>
                  24 studen
                </MenuItem>
              </Select>
            </Box>
          </Grid>
          <Grid item md={12} xs={12}>
            <Box pr={2} mr={4}>
              Guirig von
            </Box>
            <Box>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell
                      style={{
                        border: "solid 1px #e2e2e2",
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <Box pr={2} pl={2}>
                        Anfang:
                      </Box>
                      <TextField
                        style={{ width: "70%" }}
                        id="datetime-local"
                        type="datetime-local"
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true,
                        }}
                        value={startDate}
                        onChange={(e) => {
                          setStartDate(e.target.value);
                        }}
                      />
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      style={{
                        border: "solid 1px #e2e2e2",
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <Box pr={2} pl={2}>
                        Ende:
                      </Box>
                      <TextField
                        style={{ width: "70%" }}
                        id="datetime-local"
                        type="datetime-local"
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true,
                        }}
                        value={endDate}
                        onChange={(e) => {
                          setEndDate(e.target.value);
                        }}
                      />
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell style={{ border: "solid 1px #e2e2e2" }} colSpan={2}>
                      <FormControlLabel
                        id={"-checkbox-field"}
                        classes={{ root: classes.checkBoxLabel, label: classes.checkBoxLabelLabel }}
                        control={
                          <Checkbox color="success" checked={allDay === true} onChange={(e) => setAllDay(!allDay)} />
                        }
                        label={"Ganzere Tag"}
                        style={{ paddingLeft: 8 }}
                      />
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </Box>
          </Grid>
          <Grid item md={12} xs={12}>
            <Box display={"flex"} alignItems={"center"}>
              <Box pr={4}>Turnus</Box>
              <ul className={classes.listContainer}>
                <li className={`${repeat === 1 ? "selected" : ""}`} value={0} onClick={(e) => setRepeat(1)}>
                  Wochentage
                </li>
                <li className={`${repeat === 2 ? "selected" : ""}`} value={1} onClick={(e) => setRepeat(2)}>
                  2-Wochentage
                </li>
                <li className={`${repeat === 3 ? "selected" : ""}`} value={2} onClick={(e) => setRepeat(3)}>
                  3-Wochentage
                </li>
              </ul>
            </Box>
          </Grid>
          <Grid item md={12} xs={12}>
            <Box display={"flex"} alignItems={"center"}>
              <Box pr={4}>Wochentage</Box>
              <ul className={classes.listContainer}>
                <li
                  className={`${days && days.Monday ? "selected" : ""}`}
                  value={days?.Monday}
                  // onClick={(e) => {
                  //   handleChangeDay("Monday");
                  // }}
                >
                  Mo
                </li>
                <li
                  className={`${days && days.Tuesday ? "selected" : ""}`}
                  value={days?.Thursday}
                  // onClick={(e) => {
                  //   handleChangeDay("Thursday");
                  // }}
                >
                  Di
                </li>
                <li
                  className={`${days && days.Wednesday ? "selected" : ""}`}
                  value={days?.Wednesday}
                  // onClick={(e) => {
                  //   handleChangeDay("Wednesday");
                  // }}
                >
                  Mi
                </li>
                <li
                  className={`${days && days.Thursday ? "selected" : ""}`}
                  value={days?.Thursday}
                  // onClick={(e) => {
                  //   handleChangeDay("Thursday");
                  // }}
                >
                  Do
                </li>
                <li
                  className={`${days && days.Friday ? "selected" : ""}`}
                  value={days?.Friday}
                  // onClick={(e) => {
                  //   handleChangeDay("Friday");
                  // }}
                >
                  Fr
                </li>
                <li
                  className={`${days && days.Saturday ? "selected" : ""}`}
                  value={days?.Saturday}
                  // onClick={(e) => {
                  //   handleChangeDay("Saturday");
                  // }}
                >
                  Sa
                </li>
                <li
                  className={`${days && days.Sunday ? "selected" : ""}`}
                  value={days?.Sunday}
                  // onClick={(e) => {
                  //   handleChangeDay("Sunday");
                  // }}
                >
                  So
                </li>
              </ul>
            </Box>
          </Grid>
        </Grid>

        <Box></Box>
        <Box></Box>
      </DialogContent>
      <DialogActions>
        <Button variant="outlined" style={{ marginRight: "16px" }} onClick={handleClose}>
          Abbrechen
        </Button>
        {editMeeting?._id ? (
          <Button variant="contained" color="primary" onClick={handleSubmit}>
            Aktualisieren
          </Button>
        ) : (
          <Button variant="contained" onClick={handleSubmit}>
            Speichern
          </Button>
        )}
      </DialogActions>
    </Dialog>
  );
};

export default NewEventDialog;
